<template>
 <div class='projects'>
    projects page
    <project />
 </div>
</template>
<script>
import {axios} from "axios";

import project from "./project";

export default {
  components: {
    project,
  },
  data: function () {
    return {
      username : "",
      password : "",
      token: '',
    }
  },
  created: function() {
    const token = localStorage.getItem('token');
    if (token) {
      this.axios.get(process.env.BACKEND_API + '/projects',
      {
        headers: {
          Authorization: token,
        } 
      })
      .then((r) => console.log(r))
      .catch((e) => console.log(e));
    }
  } 
}
</script>

